import './App.css';
import React, { Component } from 'react';
import AliceCarousel from 'react-alice-carousel';
import "react-alice-carousel/lib/alice-carousel.css";

class ImageBanner extends Component {

  constructor(props) {
    super(props)
    this.state = {
      images: []
    }
  }

  componentDidMount() {
    fetch ('https://gardien.tokodistributor.co.id/api-web/v2/utility/home/banner-web', {
      method: 'GET'
    })
    .then((response) => {
      return response.json()
    })
    .then((data) => {
      // console.log(data.data)
      this.setState({ images:data.data })
    })
  }

  render() {
    const { images } = this.state

    return (
      <React.Fragment>
        <AliceCarousel autoPlay autoPlayInterval="1500">
        { images.map((item, index) =>
                     <img className="imgbanner" key={index} src={item.url_mobile} alt={''}/>
                    )
                  }
        </AliceCarousel>
      </React.Fragment>
    );
  }
}

class ListCategory extends Component {

  constructor(props) {
    super(props)
    this.state = {
      categories: [] 
    }
  }

  componentDidMount() {
    fetch ('https://gardien.tokodistributor.co.id/api-web/v2/utility/home/box-category?with_staple=true', {
      method: 'GET'
    })
    .then((response) => {
      return response.json()
    })
    .then((data) => {
      // console.log(data.data)
      this.setState({ categories:data.data})
    })
  }



  render() {
    const  { categories } = this.state

    return (
      <React.Fragment>
        {
          categories.map( (item, index) =>
          <div className="catItem" >
            <img className="catImg" src={ item.icon} alt='' />
            <span className="catFont" >{ item.category_name }</span>
          </div>
          ) 
        }
      </React.Fragment>
    );

  }
}

class Product extends Component {
  render(){
    return (
      <div className="productDiv">
        <img className="imgProduct" src={this.props.products.image_uri}  alt="" />
        <p className="infoProduct" > {this.props.products.product_name}</p>
        <h1 className="priceProduct"> Rp. { this.props.products.normal_price }</h1>
      </div>
    )
  }
}

class App extends Component {
  
  constructor(props) {
    super(props);
    this.state = {
      products: [] ,
      page: 1 
    }
  }

  fetchData = (pageNum) =>{
    let productUrl = 'https://gardien.tokodistributor.co.id/api-web/v2/product-recommendation?page='+pageNum;

    fetch (productUrl)
      .then(res => res.json())
      // .then(data => console.log(data))
      .then(data => {
        this.setState ({
          products: [...this.state.products,...data.data]
        })
      })
  }

  componentDidMount = () => {
    window.addEventListener('scroll', this.infiniteScroll);
    this.fetchData(this.state.page);
  }

  infiniteScroll = () => {
    // End of the document reached?
      if (window.innerHeight + document.documentElement.scrollTop === document.documentElement.offsetHeight){
      
        let newPage = this.state.page;
        newPage++;
        this.setState({
          page: newPage
        });
        this.fetchData(newPage);
        }
    }
  

  render() {
    
    return( 
      <div className="Home">
        <h1 className="title">TOKOPAKEDI</h1>
        <div className="inputan">
          <input className="Input" type="text" placeholder="Search"/>
        </div>

        <div className="Banner">
            <ImageBanner />
        </div>

        <h1 className="catTitle">List Category</h1>
        <div className="Category">
          <ListCategory />
        </div>
        <h1 className="prodTitle">Produk Terlaris</h1>
        <div className="Product">
          {
            this.state.products.map( (product,idx) => 
              (<Product key={idx} products={product} />)
            )
          }
        </div>
      </div>
    );
  }
}

export default App;
